.hero-wrapper{
    color: white;
    position: relative;
    padding-bottom: 2rem;
    background: var(--black);


}
.hero-container{
    justify-content: space-between;
    align-items: flex-end;

}
.hero-left{
    gap: 3rem;
}
.hero-title{
    position: relative;
    z-index: 1;
}
.hero-title>h1{
    font-weight: 500;
    font-size: 3.8rem;
    line-height: 4rem;
}
.orange-circle{
    height: 4rem;
    width: 4rem;
    background: var(--orange-gradient);
    border-radius: 999px;
    position: absolute;
    right: 38%;
    top: -10%;
    z-index: -1;
}
.search-bar{
    background-color: white; 
    border-radius: 5px;
    border: 3px solid rgba(120, 120, 120, 0.374);
    padding: 0.5rem 1rem;
    justify-content: space-between;
    width: 100%;


}
.search-bar>input{
    border: none;
    outline: none;
}
.stats{
    width: 100%;
    justify-content: space-between;

}
.stat>:nth-child(1){
    font-size: 2rem;
}
.stat>:nth-child(1)>:last-child{
    color: orange;
}
.image-container{
    width: 25rem;
    height: 30rem;
    overflow: hidden ;
    border-radius: 10rem 10rem 0 0;
    border: 8px solid rgba(255, 255, 255, 0.12);

}
.image-container>img{
    width: 100%;
    height: 100%;
}
.flexColStart1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  .flexColEnd1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
  }
  